
import PropTypes from 'prop-types';
import { useRef, useState } from 'react';
import Slider from 'react-slick';
// @mui
import { Box, Grid, Dialog, DialogContent, Paper, Stack, Typography } from '@mui/material';
// _mock
import _mock from '../../_mock';
// components
import Image from '../../components/Image';
import TextMaxLine from '../../components/TextMaxLine';
import { CarouselArrows } from '../../components/carousel';



// ----------------------------------------------------------------------

ProductDetailsVideoDialog.propTypes = {
  productId: PropTypes.string.isRequired,
  videoSrc: PropTypes.string,
  openModal: PropTypes.bool,
  onClosed: PropTypes.func,
}

export default function ProductDetailsVideoDialog({ productId, videoSrc, openModal, onClosed }) {
  const carouselRef = useRef(null);
  const [video, setVideo] = useState(videoSrc);

  // -------Related Product get---------
  const products =[...Array(10)].map((_,index)=>({
    id: _mock.id(index),
    price: _mock.number.price(index),
    cover: _mock.image.cover(index),
    title:_mock.text.title(index),
    video: [{
      light: _mock.image.cover(index),
      video: "https://media.w3.org/2010/05/sintel/trailer_hd.mp4",
    },
    {
      light: _mock.image.cover(index),
      video: "https://media.w3.org/2010/05/sintel/trailer_hd.mp4",
    }],
  }))


  const settings = {
    dots: false,
    arrows: true,
    autoplay: false,
    slidesToShow: 2,
  };

  const onCardClick = (id) => {
    console.log(id);
  }


  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };


  return (

    <Dialog
      aria-describedby="alert-dialog-slide-description"
      open={openModal}
      onClose={onClosed}
      fullWidth="lg"
      
    >
      <DialogContent sx={{p:0}}>
        <Grid container spacing={1} justifyContent="space-between">
          <Grid item xs={12} sm={8} >
            <Box sx={{ width: "100%", maxHeight: "60vh", height: "75%", position: 'relative', backgroundColor: 'black' }}>
              <video controls muted autoPlay
                width="100%" height="100%"
              >
                <source src={video} type="video/mp4" />
              </video>
            </Box>
            <Box sx={{ position: 'relative'  ,maxHeight:"25%"}}>
              <CarouselArrows filled onNext={handleNext} onPrevious={handlePrevious}>
                <Slider ref={carouselRef} {...settings}>
                  {products.map((item, index) => (
                    <ShowCard key={index} item={item}
                       handleClick={(id) => onCardClick(id)}
                    />
                  ))}
                </Slider>
              </CarouselArrows>
            </Box>
          </Grid>
          <Grid item xs={12} sm={4}>
             sajfajsdfjkasjdfklajsdf
             asdfjlasdj
             asdkfljas;l
          </Grid>
        </Grid>
      </DialogContent>
    </Dialog>
  );
}

// ----------------------------------------------------------------------

ShowCard.propTypes = {
  item: PropTypes.shape({
    price: PropTypes.number,
    cover: PropTypes.string,
    title: PropTypes.string,
    id: PropTypes.string,
  }),
  handleClick: PropTypes.func,
}


function ShowCard({ item, handleClick }) {
  const { title, cover, price, id} = item;
  return (
    <Paper sx={{ mx: 1, my: 2, borderRadius: 1, bgcolor: 'background.neutral' }}
      onClick={() => handleClick(id)}
    >
      <Stack direction="row" alignItems="center" spacing={1}>
        <Image src={cover} sx={{ borderRadius: 1 }} ratio="1/1" />
        <div>
          <TextMaxLine line={1}>{title}</TextMaxLine>
          <Typography variant="caption" sx={{ color: 'text.disabled', mt: 0.5, display: 'block' }}>
            {price}
          </Typography>
        </div>
      </Stack>
    </Paper>
  );
}



