import PropTypes from 'prop-types';
import { useRef, useState } from 'react';
import Slider from 'react-slick';
// @mui
import { useTheme } from '@mui/material/styles';
import { Box, Grid, Stack, Card, Popover, Button, Typography, CardHeader, CardContent } from '@mui/material';
// _mock
import _mock from '../../_mock';
// components
import Image from '../../components/Image';
import { MotionInView, varFade } from '../../components/animate';
import { CarouselArrows } from '../../components/carousel';
import ProductDetailsVideoDialog from './ProductDetailsVideoDialog';

// ----------------------------------------------------------------------

ProductDetailsVideo.propType = {
  productVideos: PropTypes.array,
  ProductId: PropTypes.string,
}

export default function ProductDetailsVideo({ productVideos, productId }) {


  const carouselRef = useRef(null);
  const theme = useTheme();

  const [modalVideo, setModalVideo] = useState(null);


  // ---------------------FAKE---------


  // ----------------------------------------

  const settings = {
    arrows: true,
    slidesToShow: 4,
    centerMode: true,
    centerPadding: '0px',
    rtl: Boolean(theme.direction === 'rtl'),
  };

  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };
  console.log('read one');
  return (
    <>
      <Box sx={{ position: 'relative', p: 3 }}>
        <Typography variant="h4" sx={{ mb: 1 }}>
          Relative Video
          {console.log('read one')}
        </Typography>
        <CarouselArrows filled onNext={handleNext} onPrevious={handlePrevious}>
          <Slider ref={carouselRef} {...settings}>
            {productVideos.map((item, index) => (
              <MotionInView key={index} variants={varFade().in} sx={{ px: 0.5, py: 1 }}>
                <Image ratio="1/1" src={item.light}
                  alt={item.light} onClick={() => setModalVideo(item)}
                  sx={{ borderRadius: 1, cursor: "point" }} />
              </MotionInView>
            ))}
          </Slider>
        </CarouselArrows>
      </Box>
      {Boolean(modalVideo) &&
        <ProductDetailsVideoDialog openModal={true}
         videoSrc={modalVideo.video} 
         onClose={() => setModalVideo(null)} productId={productId} />
      }
    </>
  );
}


